.feedCat {
  width: 100%;
  min-height: 680px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  background-image: url("../img/bg-main.jpg");
  background-size: cover;
}

.feedCat__header {
  margin-top: 47px;
  margin-bottom: 10px;
  font-family: "Exo", "Arial", sans-serif;
  font-size: 36px;
  text-align: center;
  font-weight: 100;
  color: rgba(255, 255, 255, 0.75);
}

.feed__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 13px;
}

.feed__item {
  width: 320px;
  margin: 13px 10px;
}

.feed__bg {
  padding: 4px;
  border-radius: 10px;
  clip-path: polygon(12% 0, 100% 0, 100% 100%, 0% 100%, 0% 8%);

  &--fua {
    background-color: $first_color;
  }

  &--fish {
    background-color: $second_color;
  }

  &--chicken {
    background-color: rgba(179, 179, 179, 0.5);
  }
}

.feed__wrapper {
  background-color: #fff;
  background-image: url("../img/Photo.png");
  background-position: -43px 213px;
  background-repeat: no-repeat;
  border-radius: 5px;
  clip-path: polygon(12% 0, 100% 0, 100% 100%, 0% 100%, 0% 8%);

  &--unavailable {
    .feed__main {
      background-color: rgba(242, 242, 242, 0.5);
      color: rgba(179, 179, 179, 0.5);
    }

    .feed__top {
      color: rgba(179, 179, 179, 0.5);
    }

    .feed__title {
      color: rgba(179, 179, 179, 0.5);
    }

    .feed__subtitle {
      color: rgba(179, 179, 179, 0.5);
    }

    .feed__bonus {
      color: rgba(179, 179, 179, 0.5);
    }

    .feed__weight {
      background-color: #b3b3b3;
    }
  }
}

.feed__main {
  position: relative;
  min-height: 480px;
  padding-top: 20px;
  padding-left: 42px;
}

.feed__top {
  font-size: 16px;
  color: #666666;
}

.feed__title {
  padding-top: 5px;
  font-size: 48px;
  font-weight: bold;
  color: #000000;
}

.feed__subtitle {
  font-size: 24px;
  font-weight: bold;
  color: #000000;
}

.feed__bonus {
  padding-top: 16px;
  font-size: 14px;
  line-height: 1.14;
  color: #666666;
}

.feed__weight {
  position: absolute;
  right: 15px;
  bottom: 15px;
  padding: 5px;
  width: 81px;
  height: 81px;
  border-radius: 50%;
  font-size: 21px;
  line-height: 0.52;
  text-align: center;
  color: #ffffff;

  &--number {
    font-size: 42px;
    line-height: initial;
  }
}

.feed__down {
  padding-top: 15px;
  font-size: 13px;
  text-align: center;
  color: #ffffff;

  &--chicken {
    color: #ffff66;
  }
}

.feed__link {
  font-weight: bold;
  color: #1698d9;
  text-decoration: dashed;
}

.active {
  background-color: antiquewhite;
}
